'use strict'

import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import VueValidator from 'vue-validator'

//plugins
import ToastPlugin from './plugins/toast'
import AlertPlugin from './plugins/alert'

//service
import Home from './components/Hello'
import MedicalNews from './components/medical/news/list'

Vue.use(VueResource)
Vue.use(VueRouter)
Vue.use(VueValidator)

Vue.use(ToastPlugin)
Vue.use(AlertPlugin)

/**
 * click
 */
const FastClick = require('fastclick')
FastClick.attach(document.body)

/**
 * 
 */
Vue.config.warnExpressionErrors = true
Vue.config.devtools = true
//If your web server can't handle requests encoded as application/json, you can enable the emulateJSON option. This will send the request as application/x-www-form-urlencoded MIME type, as if from an normal HTML form.
// Vue.http.options.emulateJSON = true;

/**
 * Vuefilter
 */
// Object.keys(filters).forEach(k => Vue.filter(k, filters[k]))

/**
 * VueRouter
 */
// const router = new VueRouter({
// 	hashbang: true,
// 	history: false,
// 	saveScrollPosition: true,
// 	transitionOnLoad: true
// })
const router = new VueRouter({
  transitionOnLoad: true
})

/**
 * sync router params
 */
import { sync } from 'vuex-router-sync'
import store from 'vux/src/vuex/store'

let history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)

/**
 * sync router loading status
 */
const commit = store.commit || store.dispatch

router.beforeEach(({to,from,next}) => {
	const toIndex = history.getItem(to.path)
	const fromIndex = history.getItem(from.path)
	if (toIndex) {
		if (toIndex > fromIndex) {
			commit('UPDATE_DIRECTION', 'forward')
		} else {
			commit('UPDATE_DIRECTION', 'back')
		}
	} else {
		++historyCount
		history.setItem('count', historyCount)
		to.path !== '/' && history.setItem(to.path, historyCount)
		commit('UPDATE_DIRECTION', 'forward')
	}
	commit('UPDATE_LOADING', true)
	setTimeout(next, 50)
})

router.afterEach(() => {
	commit('UPDATE_LOADING', false)
})

sync(store, router)

//
router.map({
	'/': {
		name: 'home',
		component: Home
	},
	'*': {
		component: Home
	},
	"/medical/news": {
		name: 'medicalnews',
		component: MedicalNews
	},
})


/**
 * 
 */
router.start(App, '#app')


