<template>
  <div style="height:100%;">
    <loading :show="isLoading" position="absolute"></loading>
    <view-box v-ref:view-box>
      <!--header slot-->
      <div class="vux-demo-header-box" slot="header">
        <x-header :left-options="leftOptions" :transition="headerTransition" :title="title" @on-click-title="scrollTop"></x-header>
      </div>
      <!--default slot-->
      <div :transition="'vux-pop-' + (direction === 'forward' ? 'in' : 'out')">
      	<router-view></router-view>
      </div>
      <!--bottom slot-->
      <tabbar class="vux-demo-tabbar" icon-class="vux-center" v-show="!isTabbarDemo" slot="bottom">
        <tabbar-item v-link="{path:'/'}" :selected="route.path === '/'">
        	<span class="icon-home" slot="icon"></span>
          <span slot="label"></span>
        </tabbar-item>
        <tabbar-item v-link="{path:'/medical/news'}"  :selected="route.name === 'medicalnews'" badge="9">
          <span class="icon-list" slot="icon"></span>
          <span slot="label"></span>
        </tabbar-item>
        <tabbar-item v-link="{path:'/uc'}" :selected="route.path === '/uc'" show-dot>
          <span class="icon-user" slot="icon"></span>
          <span slot="label"></span>
        </tabbar-item>
      </tabbar>
    </view-box>
  </div>
</template>
<script>
	import store from 'vux/src/vuex/store'

	import { Tabbar, TabbarItem, Loading, ViewBox, XHeader } from 'vux/src/components'

	export default {
	  components: {
	    Tabbar,
	    TabbarItem,
	    Loading,
	    ViewBox,
	    XHeader
	  },
	  store: store,
	  vuex: {
	    getters: {
	      route: (state) => state.route,
	      isLoading: (state) => state.isLoading,
	      direction: (state) => state.direction
	    }
	  },
	  methods: {
	    scrollTop () {
	      this.$refs.viewBox.$els.viewBoxBody.scrollTop = 0
	    }
	  },
	  computed: {
	    leftOptions () {
	      return {
	        showBack: this.route.path !== '/'
	      }
	    },
	    headerTransition () {
	      return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
	    },
	    title () {
	      if (this.route.path === '/') return ''
	      if (this.route.path === '/medical/news') return ''
	    }
	  }
	}
</script>
<style lang="less">

@import 'assets/css/iconfont.less';

html, body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
body {
  background-color: #fbf9fe;
}

.weui_tabbar.vux-demo-tabbar{
	position: fixed;
}
/**
* vue-router transition
*/
.vux-pop-out-transition,
.vux-pop-in-transition {
  width: 100%;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  backface-visibility: hidden;
}
.vux-pop-out-transition,
.vux-pop-in-transition,
.vux-pop-out-enter,
.vux-pop-out-leave,
.vux-pop-in-enter,
.vux-pop-in-leave {
  will-change: transform;
  height: 100%;
  position: absolute;
  left: 0;
}
.vux-pop-out-transition,
.vux-pop-out-enter {
  animation-name: popInLeft;
}
.vux-pop-out-leave {
  animation-name: popOutRight;
}
.vux-pop-in-transition,
.vux-pop-in-enter {
  perspective: 1000;
  animation-name: popInRight;
}
.vux-pop-in-leave {
  animation-name: popOutLeft;
}
@keyframes popInLeft {
  from {
    transform: translate3d(-100%, 0, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes popOutLeft {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(-100%, 0, 0);
  }
}
@keyframes popInRight {
  from {
    transform: translate3d(100%, 0, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes popOutRight {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(100%, 0, 0);
  }
}
</style>


