<template>
  <div style="height:100%;">
    <tab>
      <tab-item :selected="demo1 === ''" @click="demo1 = ''"></tab-item>
      <tab-item :selected="demo1 === ''" @click="demo1 = ''"></tab-item>
      <tab-item :selected="demo1 === ''" @click="demo1 = ''"></tab-item>
    </tab>
    <tab :line-width=1 active-color='#fc378c' :index.sync="index">
      <tab-item class="vux-center" :selected="tabnow === v" v-for="v in tabsList" @click="tabnow = v.name">{{v.name}}</tab-item>
    </tab>
    <group  title="use is-link to show arrow">
      <cell title="Privacy" is-link></cell>
    </group>
    <loading :show="isLoading" :text="text1" position="absolute"></loading>
  </div>
</template>

<script>
  import { Toast,Loading,Tab, TabItem,Cell, Group} from 'vux/src/components'
  import TS from '../libs/ts'

  export default {
    components: {
      Toast,
      Loading,
      Tab,
      TabItem,
      Cell,
      Group
    },
    data () {
      return {
        demo1: '',
        tabsList: [],
      }
    },
    ready (){
      var self = this;
      self.tabs();
    },
    methods:{
      tabs (){
        var self = this;
        //ajax
        self.$http({
          url: TS.APIS.TNGOU.INFO.CLASSIFY,
          method: TS.APIS.GET,
          headers:{
            apikey:TS.APIS.KEY,
          },
          // params:{
          // },
          // emulateJSON:true,//application/x-www-form-urlencoded
          timeout:TS.VARS.AJAX.TIMEOUT,
          before:function(){
            self.isLoading = true;
          },
        }).then(function(response) {
          self.isLoading = false;
          // success callback
          var json = response.json();
          console.log(json);
          if(json.status){
            self.tabsList = json.tngou;
          }else{
            //
            self.$vux.toast.show({
              text: TS.VARS.MSG.ERROR,
              type: "text",
            });
          }
        }, function(response) {
          self.isLoading = false;
          self.$vux.toast.show({
            text: TS.VARS.MSG.NET_ERR,
            type: "text",
          });
          // error callback
          console.log(response);
        });
      },
    },
  }
</script>
<style lang="less">

</style>


